:root {
    --color-gray: #525252;
    --color-primary: #00c4a6;
    --color-secondary: #3c7afc;
    --color-button-primary: #f29786;
    --color-button-primary-dark: #fc816a;
    --color-button-secondary: #3c7afc;
    --color-button-secondary-dark: #0076d1;
